@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  
}

body {
  font-family: 'Outfit', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*{
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
  }
body::-webkit-scrollbar {
    display: none;
}

p {
  font-size: 1.6rem;
}

h1 {
  font-size: 6rem;
}

.app {
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0,0,0,0.4);
  color: #fff;
  min-height: 750px;
}

.app:before {
  content: '';
  background: url('./assets/sunset.jpg') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.app{
-ms-overflow-style: none;  /* IE and Edge */
scrollbar-width: none;  /* Firefox */
}
.app::-webkit-scrollbar {
  display: none;
}
.search_before{
  text-align: center;
  align-content: center;

  padding: 1rem;
  margin-top: 10rem;
}
.search_after{
  text-align: center;
  align-content: center;

  padding: 1rem;
  margin-top: 2rem;
}

.app input {
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255,255,255, 0.8);
  background: rgba(255,255,255, 0.1);
  color: #f8f8f8;
}

::placeholder {
  color: #f8f8f8;
}

.container {
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.app .top {
  width: 100%;
  margin: 1rem auto;
  position: relative;
  right: -1%;

}

.app .description {
  position: relative;
  right: -1%;
  
} 

.app .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255,255,255, 0.2);
}

.contacts
{
  align-content: center;
  margin: 1.5rem;
  text-align: center;
}

.links
{
  display: flex;
  justify-content: center;
  align-items: center;
}
.links a{
  padding: 1.5rem;
}
.facebook{
  color: rgba(255,255,255, 255);
  cursor:pointer;
  
} 
.gmail{
  color: rgba(255,255,255, 255);
  cursor:pointer;
} 
.linkedin{
  color: rgba(255,255,255, 255);
  cursor:pointer;
} 
.github{
  color: rgba(255,255,255, 255);
  cursor:pointer;
} 


.linkedin :hover{
  
  color: #0077b5 ;
  /* background-color: rgba(255,255,255, 0.2); */
  transform: scale(1.2);
  transition: 400ms ease-in;
}
.github :hover{
  
  color: #171515 ;
  /* background-color: rgba(255,255,255, 0.2); */
  transform: scale(1.2);
  transition: 400ms ease-in;
}
.facebook :hover{
  transform: scale(1.2);
  color: #4267B2 ;
  /* background-color: rgba(255,255,255, 0.2); */
  transition: 400ms ease-in;
}
.gmail :hover{
  transform: scale(1.2);
  color: #BB001B ;
  /* background-color: rgba(255,255,255, 0.2); */
  transition: 400ms ease-in;
}


.bold {
  font-weight: 700;
}








